import React from "react";
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem/DilogItem";
import Message from "./Message/Message";
import {onMessageChangeActionCreator, sendMessageActionCreator} from "../../redux/dialogs-reducer";
import Dialogs from "./Dialogs";

import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";




let mapStateToProps = (state) => {
    return {
        dialogsPages: state.dialogsPages
            }
}
let mapDispatchToProps = (dispatch) => {
    return {
        /*updateNewMessagetext: (text) => {
           dispatch(onMessageChangeActionCreator(text))
        },*/
        sendMessage: (newMessageElement) => {
            dispatch(sendMessageActionCreator(newMessageElement))
        }
    }
}

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withAuthRedirect
)(Dialogs);