import React from "react";
import s from './Dialogs.module.css';
import DialogItem from "./DialogItem/DilogItem";
import Message from "./Message/Message";
import {onMessageChangeActionCreator, sendMessageActionCreator} from "../../redux/dialogs-reducer";
import {Redirect} from "react-router-dom";
import {Field, reduxForm} from "redux-form";
import {Textarea} from "../Common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validatos";

const maxLength50 = maxLengthCreator(50);
const Dialogs = (props) => {
    let state = props.dialogsPages;
    /*let newMessageElement = React.createRef();*/
    let dialogsElements = state.dialogs.map(d => <DialogItem name={d.name} id={d.id}/>);
    let messagesElements = state.messages.map(m => <Message message={m.message}/>);
   /* let sendMessage = () => {
        props.sendMessage();
    };*/
    /*let onMessageChange = () => {
        let text = newMessageElement.current.value;
        props.updateNewMessagetext(text)
    }*/
    let addNewMessage = (values) => {
        props.sendMessage(values.newMessageElement);
    }


    if (props.isAuth === false) return <Redirect to={"/login"}/>;

    return (
        <div className={s.dialogs}>

            <div className={s.dialogsItems}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}

            </div>
            <div>
                <AddMessageFormRedux onSubmit={addNewMessage}/>
            </div>

        </div>
    )

}

const AddMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name='newMessageElement' placeholder={'Enter your message'} validate={[required, maxLength50]}/>

                <button>Send Message</button>
            </div>
        </form>
    )
}
const AddMessageFormRedux = reduxForm({form:"dialogAddMessageForm"})(AddMessageForm)
export default Dialogs;